.current {
    color: red;
}

a {
  text-decoration: none;
  color: blue;
}

.artoh {
  vertical-align: top;
  display: inline;
  margin: 0px;
}

img {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.container {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* 2 columns */
  grid-template-rows: repeat(2, 1fr);    /* 2 rows */
}

.post {
   border: 1px solid black;
   padding: 5px;
   margin: 5px;
 }

body {
    font-family: "Lato";
    font-size: 16.5px;
    // margin-top: 100px;
    // margin-bottom: 100px;
    margin-right: 180px;
    margin-left: 180px;
}

